.footer {
    width: 100%;
    background: #181818;
    color: #fff;
    padding: 1.2rem 0 0.7rem 0;
    text-align: center;
    position: relative;
    z-index: 200;
    box-shadow: 0 -2px 16px 0 rgba(0,0,0,0.1);
  }
  
  .footer-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  
  .footer-links {
    display: flex;
    gap: 2rem;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 0.5rem;
  }
  
  .footer-links a {
    color: #04847f;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.2s, transform 0.2s;
    display: flex;
    align-items: center;
    font-size: 1.7rem;
    padding: 0.5rem;
    border-radius: 50%;
  }
  
  .footer-links a:hover {
    color: #FFB800;
    background: rgba(255, 184, 0, 0.08);
    transform: translateY(-2px) scale(1.15);
  }
  
  @media (max-width: 600px) {
    .footer-content {
      padding: 0 0.5rem;
    }
    .footer-links {
      gap: 1.2rem;
      flex-wrap: wrap;
      justify-content: center;
      width: 100%;
    }
    .footer-links a {
      font-size: 1.4rem;
      padding: 0.4rem;
    }
    .footer {
      padding: 1rem 0 0.5rem 0;
    }
  } 